$sgbs-icons-font-path: 'fonts/' !default;

@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  font-display: auto;
  src: url('#{$sgbs-icons-font-path}MaterialIcons-Regular.ttf');
}

@font-face {
  font-family: 'Material Icons Outlined';
  font-style: normal;
  font-weight: 400;
  font-display: auto;
  src: url('#{$sgbs-icons-font-path}MaterialIconsOutlined-Regular.otf');
}

.icon:not(.icon-outlined) {
  font-family: 'Material Icons';
}

.icon-outlined {
  font-family: 'Material Icons Outlined';
}
